import css from "./index.scss"

import * as THREE from 'three/build/three.module'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader'
import * as TWEEN from '@tweenjs/tween.js'
import 'jquery'


window.onload = function () {
    const panorama = new Panorama()
    panorama.init()
    panorama.render()

    window.addEventListener('resize', function () {
        panorama.camera.aspect = window.innerWidth / window.innerHeight
        panorama.camera.updateProjectionMatrix()
        panorama.renderer.setSize(window.innerWidth, window.innerHeight)
    })

    var percentage = 0
    setInterval(()=>{
        if(percentage<100){
            percentage++
            $(".bar").css("width",percentage+"%")
        }else{
            $(".bar").addClass("complete")
            $(".loadingPage").addClass("complete")
            $(".loadingPage").css("pointer-events","none")
            clearInterval()
        }
    },100)

        
}

class Panorama{
    constructor(){
        this.scene
        this.renderer
        this.camera
        this.cameraControl
        this.texture
        this.materials
        this.room
    }
    init(){
        //建立場景
        this.scene = new THREE.Scene()


        //建立渲染器
        this.renderer = new THREE.WebGLRenderer()
        this.renderer.setPixelRatio( window.devicePixelRatio );
        this.renderer.setSize(window.innerWidth, window.innerHeight)
        document.body.appendChild(this.renderer.domElement)



        //建立攝影機
        this.camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 0.1, 1000)
        this.camera.position.set(0, 0, 0.01)
        this.camera.lookAt(this.scene.position)

        this.cameraControl = new OrbitControls(this.camera, this.renderer.domElement)
        this.cameraControl.enableZoom = false;
        this.cameraControl.enablePan = false;
        this.cameraControl.enableDamping = true // 啟用阻尼效果
        this.cameraControl.autoRotate = true
        this.cameraControl.autoRotateSpeed = 1

        this.texture = new THREE.TextureLoader().load('panorama.jpg')
        this.material = new THREE.MeshBasicMaterial( { map: this.texture } )

        this.room = new THREE.Mesh( new THREE.SphereGeometry( 1, 1024, 1024), this.material )
        this.room.geometry.scale( 1, 1, -1)
        this.room.rotation.y  = Math.PI/2
        this.scene.add(this.room)
    }
    render(){
        TWEEN.update()
        this.renderer.render(this.scene, this.camera)
        this.cameraControl.update()
        requestAnimationFrame(()=>this.render())
    }
}